
.message {

}

.imageAndText {
    display: flex;
    flex-direction: row-reverse;

}


.imageAndText img {
    width: 40px; /* Ширина аватарки */
    height: 40px; /* Высота аватарки */
    border-radius: 50%; /* Делает аватар круглым */
    margin-right: 20%;
    margin-top: 10px;
    margin-left: 5px;
}


.text {

    height: 100%; /* Обеспечиваем, что блок заполнит доступное пространство */
    padding: 10px; /* Отступы вокруг блока */
    margin: 10px 0; /* Отступы вокруг каждого сообщения */
    background-color: #007bff;
    color: white;
    width: 20%;
    position: relative;
    margin-left: 60%;
    border-radius: 20px

}

.message:after {

}

.name {

    top: 10px; /* Отступ сверху */
    right: 100px; /* Отступ справа */
    font-weight: bold; /* Жирное начертание текста для имени */
    color: white;
    padding-left: 85%;
}

.messageText {
    margin-top: auto; /* Сдвинуть текст сообщения вниз */
    text-align: right; /* Выравнивание текста по правому краю (по желанию) */
}

.time {
    padding-left: 80%;
    font-size: 12px; /* Меньший шрифт для времени */
    color: black; /* Цвет текста времени */
    margin-top: 5px; /* Отступ сверху для отделения от аватарки */
}