
.sendForm {
    display: flex; /* Используем Flexbox для размещения элементов в строку */
    justify-content: center; /* Центрируем элементы формы */
    align-items: center; /* Выравниваем по вертикали */
    bottom: 20px; /* Отступ от нижнего края */
    left: 50%; /* Центрируем по горизонтали */
    transform: translateX(-50%); /* Сдвиг на половину ширины для точного центрирования */
    width: 100%; /* Ширина формы */
    max-width: 1000px; /* Максимальная ширина формы */
    padding: 10px; /* Отступы внутри формы */

}

.textarea {
    flex-grow: 1; /* Поле ввода занимает все доступное пространство */
    padding: 10px; /* Отступы внутри текстового поля */
    border: 1px solid #ccc; /* Граница текстового поля */
    border-radius: 25px; /* Закругление углов текстового поля */
    resize: none; /* Запрет изменения размера */
    margin-right: 10px; /* Отступ между текстовой областью и кнопкой */
    background: lightgrey;
    min-height: 35px; /* Минимальная высота текстового поля */
    height: 35px; /* Фиксированная высота текстового поля */
    font-size: 12px;
}

.button {
    padding: 10px 20px; /* Отступы внутри кнопки */
    border: none; /* Без границ */
    background-color: #007bff; /* Синий цвет фона для кнопки */
    color: white; /* Цвет текста кнопки */
    border-radius: 25px; /* Закругление углов кнопки */
    cursor: pointer; /* Указатель при наведении */
}

.button:hover {
    background-color: #0056b3;
}
